<template>
  <div class="changPhone">
    <header class="mui-bar mui-bar-nav" style="box-shadow: none">
      <a class="mui-icon mui-icon-left-nav mui-pull-left" @click="goBack" style="color:#4595e5"></a>
      <h1 class="mui-title"></h1>
    </header>
    <div class="mui-content" v-if="yanShow">
      <p style="text-align: left;font-size: .3rem;margin:.3rem"></p>
      <div style="padding:0 .3rem">
        <ul class="mui-table-view">
          <li class="mui-table-view-cell" style="padding:0.2rem;font-size: 0.3rem">{{userPhoneShow}}</li>
        </ul>
        <ul class="mui-table-view" style="margin: .4rem 0">
          <input type="number" placeholder="" v-model="inpPhone" class="mui-input-clear" style="width:100%;border:none;margin-bottom: 0;text-align: center"/>
        </ul>
        <mt-button type="primary" style="width:100%" @click="yanZheng"></mt-button>
      </div>
    </div>

    <div class="mui-content" v-if="!yanShow">
      <p style="text-align: left;font-size: .3rem;margin:.3rem"></p>
      <div style="padding:0 .3rem">
        <ul class="mui-table-view">
          <input type="number" placeholder="" v-model="inpNewPhone" class="mui-input-clear" style="width:100%;border:none;margin-bottom: 0;text-align: center"/>
        </ul>
        <ul class="mui-table-view" style="margin: .4rem 0">
          <input type="number" placeholder="" v-model="newYanma" class="mui-input-clear" style="width:100%;border:none;margin-bottom: 0;text-align: center"/>
        </ul>
        <mt-button disabled style="width:100%" v-if="inpNewPhone.length!=11"></mt-button>
        <mt-button type="primary" style="width:100%" v-if="inpNewPhone.length==11" id="yzm" @click="getYan"></mt-button>
       <!-- <button type="button" class="mui-btn mui-btn-primary" style="width:100%;" @click="getYan" v-if="inpNewPhone.length==11" id="yzm"></button>-->
        <mt-button type="primary" style="width:100%;margin-top: .4rem" @click="bangDing"></mt-button>
      </div>
    </div>
  </div>
</template>

<script>
  import Api from '../commom/API.js';
  import {setCookie,getCookie} from '../commom/cookie.js'
  import { Toast } from 'mint-ui';
  export default {
    data(){
      return {
        userID:'',
        inpPhone:'',
        userPhoneShow:'',
        userPhoneYan:'',
        inpNewPhone:'',
        newYanma:'',
        yanShow:true,
        nextGet:false
      }
    },
    mounted(){
      this.getInformation()
    },
    methods: {
      //
      goBack:function () {
        this.$router.go(-1)
      },
      // 
      getInformation(){
        /*cookie*/
        this.userID = getCookie('userId');
        /*cookie*/
        if (this.userID == "") {
          this.$router.push('/login')
        }else{
          let data = {'userId': this.userID};
          /**/
          var url = Api.Apis + '/h5/h5MyInfo/getUserInfo';
          this.$http.post(url, data, {emulateJSON: true}//POSTemulateJSONtrue
          ).then((data) => {
            if (data.status == 200 && data.ok == true) {
              var datas = data.body.data;
              if (datas.state == "success") {
                this.userPhoneShow = datas.user.userPhone.substr(0, 3) + '****' + datas.user.userPhone.substr(7);
                this.userPhoneYan = datas.user.userPhone;
              }
            }
          })
        }
      },
      //
      yanZheng(){
          if(this.inpPhone == this.userPhoneYan){
            this.yanShow = false
          }else{
            Toast({message: '',position: 'bottom'})
          }
      },
      //
      getYan(){
          this.nextGet = true;
          let pattern = /^((1[3,5,8][0-9])|(14[5,7])|(17[0,3,6,7,8])|(19[7]))\d{8}$/;
          if(!(pattern.test(this.inpNewPhone))) {
            Toast({message: '',position: 'bottom'});
            return;
          };
          // 
          var num = 60;
          var sendVerificationCode = document.getElementById("yzm");
          sendVerificationCode.disabled=true;
          // 
          let url = Api.Apis + '/h5/h5sendsms/sendSMSForRegister';
          let data = {'phone': this.inpNewPhone};
          this.$http.post(url, data, {emulateJSON: true}
          ).then(function (data) {
            if (data.status == 200 && data.ok == true) {
              var datas = data.body;
              if (datas.state == "success") {
                Toast({message: datas.successMsg,position: 'bottom'});
                function daojishi() {
                  num--;
                  sendVerificationCode.innerHTML = num + 's';
                  if (num < 1) {
                    sendVerificationCode.innerHTML = "";
                    clearInterval(timer, 60000);
                    sendVerificationCode.disabled=false;
                  }
                };
                var timer = setInterval(daojishi, 1000);
              } else {
                Toast({message: datas.errMsg,position: 'bottom'});
              }
            }
          });
        },
      //
      bangDing(){
        if (this.inpNewPhone.length == 0) {
          Toast({message: '',position: 'bottom'});
          return;
        }
        if (this.newYanma.length == 0) {
          Toast({message: '',position: 'bottom'});
          return;
        }
        if(this.nextGet){
          let data = {'userId': this.userID,'phone':this.inpNewPhone,'code':this.newYanma};
          /**/
          var url = Api.Apis + '/h5/h5MyInfo/updatePhone';
          this.$http.post(url, data, {emulateJSON: true}//POSTemulateJSONtrue
          ).then((data) => {
            if (data.status == 200 && data.ok == true) {
              var datas = data.body.data;
              if (datas.state == "success") {
                this.$router.push('/pZiliao')
              }
            }
          })
        }else{
          Toast({message: '',position: 'bottom'});
        }
      }
    }
  }
</script>


<style type="text/css" scoped>

</style>



